<template>
  <section class="add-todo-form">
    <div class="input-field">
      <input id="title" type="text" class="validate" v-model="title" />
      <label for="title">Title</label>
    </div>
    <div class="input-field">
      <input
        id="description"
        type="text"
        class="validate"
        v-model="description"
      />
      <label for="description">Description</label>
    </div>
    <a class="waves-effect waves-teal btn-flat" @click="onAddClick">Add Todo</a>
 
  </section>
</template>
 
<script>
export default {
  name: "AddTodo",
  data() {
    return {
      title: "",
      description: "",
    };
  },
  methods: {
    onAddClick() {
      if (this.title.trim() && this.description.trim()){
        const newTodo = {
          title: this.title.trim(),
          description: this.description.trim(),
        };
        this.$emit("onAddTodo", newTodo);
      }
      this.title = this.description = "";
    },
  },
};
</script>
 
<style scoped>
 
.add-todo-form {
  text-align: center;
}
 
</style>