<template>
  <main>
    <AddTodo @onAddTodo="AddTodo" />
    <TodoList :todolist="todos" @onRemoveTodo="RemoveTodo" />
  </main>
</template>
 
<script>
import AddTodo from "@/components/AddTodo";
import TodoList from "@/components/Todolist";
 
export default {
  name: "App",
 
  components: {
    AddTodo,
    TodoList,
  },
 
  data: () => ({ todos: [] }),
 
  methods: {
    AddTodo(newTodo) {
      this.todos.push(newTodo);
    },
 
    RemoveTodo(id) {
      this.todos.splice(
        this.todos.findIndex((el) => el.id === id),
        1
      );
    },
  }
};
</script>
 
<style scoped>
main {
  width: 450px;
  margin: 0 auto;
}
</style>