<template>
  <section class="todo-list">
    <ul class="collection" v-if='todolist.length'>
      <li class="collection-item" v-for="todo in todolist" :key="todo.id">
        <h4>{{ todo.title }}</h4>
        <p>
          {{ todo.description }}
        </p>
        <a class="waves-effect waves-teal btn-flat" @click='onRemoveTodo(todo.id)'>Remove todo</a>
      </li>
    </ul>
    <h2 v-else>No Todos</h2>
  </section>
</template>
<script>
export default {
  name: "TodoList",
  props: ["todolist"],
  methods: {
    onRemoveTodo(id){
      this.$emit('onRemoveTodo', id)
    }
  },
};
</script>
 
<style scoped>
  .todo-list {
    text-align: center;
  }
</style>